.suggestion-item {
  text-align: left;
  margin-bottom: 0px;
  -webkit-box-shadow: 0 0 1px #aaa;
          box-shadow: 0 0 1px #aaa;
  border-radius: 2px;
  cursor: pointer;
  /*to contain the floats below*/
  overflow: hidden;
}

.suggestion-active {
  background-color: #d8d8d8;
}

.suggestion-item p {
  padding: 7px 12px;
}

.suggestion-item p.display-name {
  float: left;
}

.suggestion-item p.type {
  float: right;
  text-align: right;
  font-weight: bold;
  color: #777;
}



